import { Breadcrumbs, List, ListItem, ListItemText, Typography } from "@material-ui/core";
import { Link } from "gatsby";
import * as React from "react";
import { useUserInfo } from "shared/components/save";
import { useModalTheme } from "shared/components/style";
import Defer from "shared/components/ui/Defer";
import Navbar from "shared/components/ui/Navbar";
import Yes from '@material-ui/icons/CheckCircle'
import No from '@material-ui/icons/NotInterested'

const Page = () => {
    const modalTheme = useModalTheme();
    const user = useUserInfo();
    return (
        <div>
            <Navbar />
            <Defer data={user}>
                {() => (
                    <List>

                        <ListItem divider>
                            <Breadcrumbs>
                                <Link color="inherit" to="/user/">
                                    Layanan Desa
                                </Link>
                                <Typography color="textPrimary">Informasi Layanan</Typography>
                            </Breadcrumbs>
                        </ListItem>
                        <ListItem>
                            <ListItemText primary={'Desa / Kelurahan'} />
                            <ListItemText primary={user.geolookup.kelurahan} className={modalTheme.right} />
                        </ListItem>
                        <ListItem>
                            <ListItemText primary={'Kecamatan'} />
                            <ListItemText primary={user.geolookup.kecamatan} className={modalTheme.right} />
                        </ListItem>
                        <ListItem>
                            <ListItemText primary={'Kota / Kabupaten'} />
                            <ListItemText primary={user.geolookup.kabupaten} className={modalTheme.right} />
                        </ListItem>

                        <ListItem divider>
                            <ListItemText primary={'Provinsi'} />
                            <ListItemText primary={user.geolookup.provinsi} className={modalTheme.right} />
                        </ListItem>

                        <ListItem>
                            <ListItemText primary={'Layanan Belanja'} />
                            <ListItemText primary={user.features.market ? <Yes/> :  <No/>} className={modalTheme.right} />
                        </ListItem>

                        <ListItem>
                            <ListItemText primary={'Layanan Antar'} />
                            <ListItemText primary={user.features.driver ? <Yes/> :  <No/>} className={modalTheme.right} />
                        </ListItem>

                        <ListItem>
                            <ListItemText primary={'Layanan Surat'} />
                            <ListItemText primary={user.features.mail ? <Yes/> :  <No/>} className={modalTheme.right} />
                        </ListItem>

                        <ListItem>
                            <ListItemText primary={'Layanan Komplain'} />
                            <ListItemText primary={user.features.complain ? <Yes/> :  <No/>} className={modalTheme.right} />
                        </ListItem>

                        <ListItem>
                            <ListItemText primary={'Layanan Darurat'} />
                            <ListItemText primary={user.features.emergency ? <Yes/> :  <No/>} className={modalTheme.right} />
                        </ListItem>

                    </List>
                )}
            </Defer>
        </div>
    );
}

export default Page
